@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1238px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f9fbfd;
  background-color: #011c40;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.nav-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.header__container {
  width: calc(100% - 45px);
  position: absolute;
  z-index: 1;
  display: flex;
  padding: 35px 0;
}
@media (max-width: 1023px) {
  .header__container {
    padding: 30px 0;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 20px 0;
  }
}

.header__navigation-wrapper {
  width: calc(100% - 35px);
  max-width: 1238px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__navigation-wrapper {
    width: calc(100% - 48px);
  }
}
@media (max-width: 767px) {
  .header__navigation-wrapper {
    width: calc(100% - 35px);
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-auto-rows: min-content;
    row-gap: 32px;
  }
}

@media (max-width: 767px) {
  [data-nav=is-shown] {
    width: 100%;
    background-color: #f9fbfd;
  }
  [data-nav=is-shown] .header__navigation-wrapper {
    padding-left: 15px;
  }
  [data-nav=is-shown] .header__logo {
    color: #011c40;
  }
  [data-nav=is-shown] .header__nav {
    display: block;
    align-items: baseline;
  }
  [data-nav=is-shown] .header__nav-list {
    display: block;
  }
}

@media (max-width: 767px) {
  [data-js=false] [data-nav=is-shown] {
    position: relative;
  }
  [data-js=false] [data-nav=is-shown] .header__navigation-wrapper {
    width: 100%;
    grid-template-columns: 1fr;
  }
}

@media (max-width: 767px) {
  [data-js=true] {
    width: 100%;
  }
  [data-js=true] .header__nav-toggle {
    display: flex;
    color: #011c40;
  }
  [data-js=true] [data-nav=is-shown] {
    position: fixed;
    height: 100%;
  }
  [data-js=true] [data-nav=is-shown] .header__navigation-wrapper {
    justify-content: flex-start;
  }
  [data-js=true] [data-nav=is-shown] .header__toggle-close {
    display: block;
    align-self: center;
  }
  [data-js=true] [data-nav=is-shown] .header__toggle-burger {
    display: none;
  }
}

@media (max-width: 767px) {
  [data-nav=is-hidden] {
    width: calc(100% - 15px);
  }
  [data-nav=is-hidden] .header__navigation-wrapper {
    width: calc(100% - 16px);
  }
  [data-nav=is-hidden] .header__nav {
    display: none;
  }
  [data-nav=is-hidden] .header__toggle-burger {
    display: block;
    align-self: center;
  }
  [data-nav=is-hidden] .header__toggle-close {
    display: none;
  }
}

.header__logo {
  display: flex;
  color: #f9fbfd;
}

.header__logo svg {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 42px;
    height: 22px;
  }
}

.header__nav-list {
  display: flex;
}

.header__nav-item + .header__nav-item {
  padding-left: 33px;
}
@media (max-width: 1023px) {
  .header__nav-item + .header__nav-item {
    padding-left: 23px;
  }
}
@media (max-width: 767px) {
  .header__nav-item + .header__nav-item {
    padding: 10px;
  }
}

@media (max-width: 767px) {
  .header__nav-item {
    padding: 10px 10px 8px;
    margin-bottom: 7px;
    border-radius: 10px;
  }
}
.header__nav-item:hover {
  background-color: rgba(229, 238, 247, 0.5);
}

.header__nav-link {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__nav-link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__nav-link {
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
  }
}

.header__nav-toggle {
  display: none;
  width: 28px;
  height: 28px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-radius: 50%;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .header__nav-toggle {
    justify-content: center;
    cursor: pointer;
  }
  .header__nav-toggle:hover {
    background-color: rgba(229, 238, 247, 0.5);
  }
}

.header__toggle-icon {
  align-self: center;
}

.intro {
  padding-top: 270px;
  padding-bottom: 277px;
  background-color: #101a56;
  background-image: image-set(url("../img/background/bg-intro@2x.webp") type("image/webp") 2x, url("../img/background/bg-intro.webp") type("image/webp") 1x, url("../img/background/bg-intro@2x.jpg") 2x, url("../img/background/bg-intro.jpg") 1x);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 25%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .intro {
    padding-bottom: 274px;
    background-position: 50% 25%;
  }
}
@media (max-width: 767px) {
  .intro {
    padding-top: 175px;
    padding-bottom: 179px;
    background-position: 48% 32%;
    background-size: auto calc(100% + 145px);
  }
}

.intro h1,
.intro p {
  padding-left: 95px;
  margin: 0;
  color: #f9fbfd;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .intro h1,
.intro p {
    padding-left: 0;
  }
}

.intro h1 {
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .intro h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .intro h1 {
    font-size: 36px;
    line-height: 45px;
  }
}

.intro p {
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .intro p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .intro p {
    font-size: 16px;
  }
}

.footer {
  padding: 50px 0;
  margin-top: auto;
  background-color: #011c40;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__container {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__navigation {
  display: flex;
}
@media (max-width: 767px) {
  .footer__navigation {
    flex-direction: column;
  }
}

.footer__logo {
  display: flex;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .footer__logo {
    margin-bottom: 30px;
  }
}

.footer__nav {
  display: flex;
  align-items: center;
}

.footer__nav-list {
  display: flex;
}
@media (max-width: 767px) {
  .footer__nav-list {
    flex-direction: column;
  }
}

.footer__nav-item + .footer__nav-item {
  padding-left: 33px;
}
@media (max-width: 1023px) {
  .footer__nav-item + .footer__nav-item {
    padding-left: 23px;
  }
}
@media (max-width: 767px) {
  .footer__nav-item + .footer__nav-item {
    padding: 0;
  }
}

@media (max-width: 767px) {
  .footer__nav-item {
    margin-bottom: 23px;
  }
}

.footer__nav-link {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .footer__nav-link {
    font-size: 14px;
    line-height: 17px;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.footer__social-item + .footer__social-item {
  padding-left: 27px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(1, 28, 64, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

